// ============================================================================
// 踏月寻仙 - MVU 变量结构定义 (入口文件)
// ============================================================================
// 从子模块导入并组合最终 Schema

import { z } from 'zod';

// 子模块导入
// import { AchievementSystemSchema } from './schema/achievements'; // 成就系统暂时禁用
import { CharacterLibEntrySchema, CompanionSchema, DEFAULT_CHARACTER_LIB, NpcSchema } from './schema/characters';
import { ProtagonistSchema } from './schema/protagonist';
import { OpportunitySchema, QuestSchema, ReputationSystemSchema } from './schema/systems';
import { DEFAULT_FACTIONS, DEFAULT_LOCATIONS, DEFAULT_TREASURES, FactionSchema, LocationSchema, PhysiqueSchema, SpiritRootSchema, TechniqueSchema, TreasureSchema } from './schema/world';

// 重新导出所有子模块的公开内容
// export * from './schema/achievements'; // 成就系统暂时禁用
export { CharacterLibEntrySchema, CompanionSchema, DEFAULT_CHARACTER_LIB, NpcSchema } from './schema/characters';
export { computeRealmInfo, InventorySchema, ItemSchema, SkillSchema, 品阶映射, 熟练度映射 } from './schema/common';
export { CONFIG, REALM_LIFESPANS, REALM_NAMES, REALM_STAGES, REALM_THRESHOLDS } from './schema/constants';
export { ProtagonistSchema } from './schema/protagonist';
export { OpportunitySchema, QuestSchema, ReputationEntrySchema, ReputationSystemSchema } from './schema/systems';
export { calculateBaseCombatPower, evaluateCombatPower, getDangerColor, getRealmColor, getRootColor, parseRealmToLevel } from './schema/utils';
export { DEFAULT_FACTIONS, DEFAULT_LOCATIONS, DEFAULT_TREASURES, FactionSchema, LocationSchema, PhysiqueSchema, SpiritRootSchema, TechniqueSchema, TreasureSchema } from './schema/world';

// ============================================================================
// 完整的 MVU 变量结构
// ============================================================================

export const Schema = z.object({
    世界时钟: z.object({
        纪元: z.string().prefault('末法时代'),
        年份: z.coerce.number().prefault(1),
        月份: z.coerce.number().transform(v => _.clamp(v, 1, 12)).prefault(1),
        日期: z.coerce.number().transform(v => _.clamp(v, 1, 30)).prefault(1),
        时辰: z.string().prefault('子时'),
    }).prefault({
        纪元: '末法时代',
        年份: 1,
        月份: 1,
        日期: 1,
        时辰: '子时',
    }),

    世界地图: z.record(z.string().describe('区域名'), z.object({
        layer: z.enum(['天层', '地层', '下层']).prefault('地层'),
        danger: z.coerce.number().transform(v => _.clamp(v, 0, 100)),
        desc: z.string().prefault(''),
        connections: z.array(z.string()).prefault([]),
    })).prefault({}),

    世界图志: z.record(z.string().describe('事件名'), z.object({
        状态: z.string().prefault(''),
        事件: z.string().prefault(''),
    })).prefault({}),

    宗门势力库: z.record(z.string().describe('宗门名'), FactionSchema).prefault(DEFAULT_FACTIONS),

    // 功法库 - AI 自由创造，只需提供 { 阶, 性, 效 }
    功法库: z.record(z.string().describe('功法名'), TechniqueSchema).prefault({}),

    // 法宝库 - 仅保留剧情关键法宝，其他 AI 自由创造
    法宝库: z.record(z.string().describe('法宝名'), TreasureSchema).prefault(DEFAULT_TREASURES),

    地点库: z.record(z.string().describe('地点名'), LocationSchema).prefault(DEFAULT_LOCATIONS),

    // 灵根库 - AI 自由创造，只需提供 { 质, 性, 稀 }，速度和特性由 transform 自动计算
    灵根库: z.record(z.string().describe('灵根名'), SpiritRootSchema).prefault({}),

    // 体质库 - AI 自由创造，只需提供 { 质, 特, 稀 }，优势由 transform 自动计算
    体质库: z.record(z.string().describe('体质名'), PhysiqueSchema).prefault({}),

    本尊: ProtagonistSchema,

    红颜角色库: z.record(z.string().describe('角色名'), CharacterLibEntrySchema).prefault(DEFAULT_CHARACTER_LIB),

    红颜: z.record(z.string().describe('红颜名'), CompanionSchema).prefault({}),

    // NPC图鉴 - 极简版（保留向后兼容）
    NPC图鉴: z.record(z.string().describe('NPC名'), NpcSchema).prefault({}),

    // 任务列表 - 移除自动清理逻辑，保持数据完整性
    任务列表: z.record(z.string().describe('任务ID'), QuestSchema).prefault({}),

    // 声望系统 - 健壮性与智能管理优化
    声望系统: ReputationSystemSchema,

    // ========== 成就/里程碑系统（暂时禁用） ==========
    // 成就系统: AchievementSystemSchema,

    // ========== 行动提示系统 ==========
    可参与机遇: z.array(OpportunitySchema).prefault([]),

    // 当前处境描述（用于行动提示面板顶部显示）
    当前处境: z.string().prefault(''),
});

// 导出 Schema 类型
export type SchemaType = z.output<typeof Schema>;
