// 红颜（动态角色数据）Schema
export const CompanionSchema = z
    .object({
        等级: z.coerce.number().transform(v => _.clamp(v, 1, 36)).prefault(1),
        修为: z.coerce.number().transform(v => Math.max(0, v)).prefault(0),
        灵根: z.string().prefault('五行杂灵根'),
        体质: z.string().prefault('凡体'),
        功法: z.string().prefault('无'),
        本命兵器: z.string().prefault('无'),
        神通列表: z.record(z.string().describe('神通名'), SkillSchema).prefault({}),
        灵石: z.coerce.number().transform(v => Math.max(0, v)).prefault(0),
        已活岁月: z.coerce.number().transform(v => Math.max(0, v)).prefault(0),
        尝试突破: z.boolean().prefault(false),
        好感度: z.coerce.number().transform(v => _.clamp(v, -100, 100)).prefault(0),
        关系: z.string().prefault('陌生人'),
        重要性: z.string().prefault('路人'),
    })
    .prefault({
        等级: 1,
        修为: 0,
        灵根: '五行杂灵根',
        体质: '凡体',
        功法: '无',
        本命兵器: '无',
        神通列表: {},
        灵石: 0,
        已活岁月: 0,
        尝试突破: false,
        好感度: 0,
        关系: '陌生人',
        重要性: '路人',
    })
    .transform(data => {
        const realmInfo = computeRealmInfo(data, false);
        return { ...data, ...realmInfo };
    });

// NPC图鉴 Schema - 极简版
export const NpcSchema = z.object({
    等级: z.coerce.number().transform(v => _.clamp(v, 1, 36)).prefault(1),
    所在宗门: z.string().prefault('散修'),
    备注: z.string().prefault(''),
});