// ============================================================================
// 踏月寻仙 - 系统 Schema（任务列表、声望系统、可参与机遇、当前处境）
// ============================================================================

import { z } from 'zod';

// 任务列表 Schema - 移除自动清理逻辑，保持数据完整性
export const QuestSchema = z.object({
    名称: z.string().prefault(''),
    类型: z.enum(['主线', '支线', '每日', '临危受命', '秘境探索']).prefault('主线'),
    目标: z.string().prefault(''),
    状态: z.enum(['进行中', '已完成', '已失败']).prefault('进行中'),
    // 秘境专属字段（仅当类型为秘境探索时需要）
    秘境信息: z
        .object({
            域: z.enum(['天层', '神州', '东苍', '南炎', '西庚', '北冥', '下层', '四海']).optional(),
            危: z.coerce.number().transform(v => _.clamp(v, 0, 100)).optional(),
            特: z.string().optional(),
            奖: z.union([
                z.array(z.string()),
                z.string().transform(v => v ? [v] : []),
            ]).prefault([]),
            限: z.string().optional(),
        })
        .optional(),
    创建时间: z.union([
        z.coerce.number(), // 兼容已有的时间戳
        z.string().transform(() => {
            // AI 输入修仙时间格式时，简化处理：直接用当前时间
            return Date.now();
        })
    ]).prefault(() => Date.now()),
});

// 声望条目 Schema
export const ReputationEntrySchema = z.object({
    值: z.coerce.number().transform(v => _.clamp(v, -100, 100)).prefault(0),
    关系: z.string().prefault('陌生'),
    更新时间: z.coerce.number().prefault(() => Date.now()),
});

// 声望系统 Schema（带自动关系计算 transform）
export const ReputationSystemSchema = z
    .record(z.string().describe('势力名'), ReputationEntrySchema)
    .prefault({})
    .transform(factions => {
        // 自动根据声望值计算关系描述
        return _(factions)
            .mapValues(faction => {
                const value = faction.值;
                let 自动关系: string;

                if (value >= 80) {
                    自动关系 = '盟友';
                } else if (value >= 60) {
                    自动关系 = '友善';
                } else if (value >= 30) {
                    自动关系 = '友好';
                } else if (value >= 10) {
                    自动关系 = '中立偏好';
                } else if (value >= -10) {
                    自动关系 = '中立';
                } else if (value >= -30) {
                    自动关系 = '中立偏恶';
                } else if (value >= -60) {
                    自动关系 = '敌对';
                } else if (value >= -80) {
                    自动关系 = '仇恨';
                } else {
                    自动关系 = '不死不休';
                }

                // 如果用户手动设置了关系，优先使用用户设置；否则使用自动计算的关系
                const 最终关系 = faction.关系 && faction.关系 !== '陌生' ? faction.关系 : 自动关系;

                return {
                    ...faction,
                    关系: 最终关系,
                };
            })
            .value();
    });

// 可参与机遇 Schema
export const OpportunitySchema = z.object({
    名称: z.string().prefault(''),
    来源: z.string().prefault(''), // 如：剧情推进、任务触发、NPC邀请、地点发现、时限事件
    类型: z.enum(['探索', '任务', '交易', '结交', '争夺', '修炼', '红颜', '随机']).prefault('探索'),
    描述: z.string().prefault(''),
    回报预期: z.string().prefault(''), // 如：中等灵石收益、可能获得功法线索
    风险评估: z.string().prefault(''), // 如：低风险、有战斗可能
    时限: z.string().optional(), // 如：本日结束前、三日内
    关联事件: z.string().optional(), // 关联的世界事件或任务
    优先级: z.coerce.number().transform(v => _.clamp(v, 1, 5)).prefault(3), // 1-5，5最高
});